@import '~assets/styles/h3Imports';

.root {
  padding: 1em;

  .content {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
  }

  .header {
    align-items: center;
    display: flex;

    .title {
      line-height: line-height(24, 19);
      margin: 0;
    }
  }

  .body {
    min-height: em(128);
    padding: 1em 0;

    .description {
      line-height: line-height(24, 16);
      margin: 0;
    }
  }

  .footer {
    --button-width: 48%;

    display: flex;
    justify-content: space-between;

    [data-variant='text'] {
      font-size: em(16);
      margin-left: -0.3em;
      padding: 0;
    }
  }
}
